import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { AlertTriangle, Clock, AlertCircle, Shield, Gift, CheckCircle, RefreshCw, Lock } from 'lucide-react';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { doc, onSnapshot } from 'firebase/firestore';
import { db } from '@/firebase';
import { toast } from 'sonner';
import { FreeTrialService } from '@/utils/freeTrialService';
import { isSubscriptionValid, activateUserSubscription, SubscriptionPlan } from '@/utils/subscriptionService'
import { createSubscriptionWhatsAppLink } from '@/utils/whatsappService';

export default function PendingApprovalPage() {
  const { user, signOut, userSubscription, isSubscriptionActive, checkSubscriptionStatus } = useFirebaseAuth();
  const navigate = useNavigate();
  const [isActivatingTrial, setIsActivatingTrial] = useState(false);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [hasUsedTrial, setHasUsedTrial] = useState(false);
  const [isOnFreeTrial, setIsOnFreeTrial] = useState(false);
  const [trialDurationHours, setTrialDurationHours] = useState<number>(4);
  
  // ÿ™ŸÉŸàŸäŸÜ ÿßŸÑÿ®ÿßŸÇÿßÿ™ ÿ®ŸÜŸÅÿ≥ ÿ™ÿµŸÖŸäŸÖ ÿßŸÑŸÜÿßŸÅÿ∞ÿ© ÿßŸÑŸÖŸÜÿ®ÿ´ŸÇÿ©
  const PLANS = [
    {
      title: 'ÿßŸÑÿ®ÿßŸÇÿ© ÿßŸÑÿ¥Ÿáÿ±Ÿäÿ©',
      price: 150,
      features: ['ÿØÿπŸÖ ŸÖÿ¨ÿßŸÜŸä ŸÉÿßŸÖŸÑ', 'ÿ™ÿ≠ÿØŸäÿ´ÿßÿ™ ŸÖÿ≥ÿ™ŸÖÿ±ÿ©', 'ÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑŸÖÿ®ÿßÿ¥ÿ± ŸÖŸÜ ÿßŸÑÿ™ÿ∑ÿ®ŸäŸÇ', '20 ŸÖÿ≠ŸÅÿ∏ÿ©'],
      bg: 'from-blue-50 to-indigo-50',
    },
    {
      title: 'ÿßŸÑÿ®ÿßŸÇÿ© ÿßŸÑÿ±ÿ®ÿπ ÿ≥ŸÜŸàŸäÿ©',
      price: 350,
      features: ['ÿØÿπŸÖ ŸÖŸÖŸäÿ≤', 'ÿ≠ÿ™Ÿâ 30 ŸÖÿ≠ŸÅÿ∏ÿ©', 'ÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑŸÖÿ®ÿßÿ¥ÿ± ŸÖŸÜ ÿßŸÑÿ™ÿ∑ÿ®ŸäŸÇ', 'ÿßÿ≥ÿ™ŸÇÿ®ÿßŸÑ ÿ±ÿ≥ÿßÿ¶ŸÑ ÿßŸÑÿ≥ÿ≠ÿ® ŸàÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿπŸÑŸâ ÿ™ŸÑÿ¨ÿ±ÿßŸÖ'],
      bg: 'from-emerald-50 to-green-50',
    },
    {
      title: 'ÿßŸÑÿ®ÿßŸÇÿ© ÿßŸÑÿ≥ŸÜŸàŸäÿ©',
      price: 1000,
      features: ['ÿØÿπŸÖ ŸÖŸÖÿ™ÿßÿ≤', 'ÿπÿØÿØ ŸÖÿ≠ÿßŸÅÿ∏ ŸÖŸÅÿ™Ÿàÿ≠', 'ÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑŸÖÿ®ÿßÿ¥ÿ± ŸÖŸÜ ÿßŸÑÿ™ÿ∑ÿ®ŸäŸÇ', 'ÿßÿ≥ÿ™ŸÇÿ®ÿßŸÑ ÿ±ÿ≥ÿßÿ¶ŸÑ ÿßŸÑÿ≥ÿ≠ÿ® ŸàÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿπŸÑŸâ ÿ™ŸÑÿ¨ÿ±ÿßŸÖ'],
      bg: 'from-purple-50 to-pink-50',
    },
    {
      title: 'ÿ®ÿßŸÇÿ© ŸÖÿØŸâ ÿßŸÑÿ≠Ÿäÿßÿ©',
      price: 3000,
      features: ['ÿØÿπŸÖ ŸÖŸÖÿ™ÿßÿ≤', 'ÿπÿØÿØ ŸÖÿ≠ÿßŸÅÿ∏ ŸÖŸÅÿ™Ÿàÿ≠', 'ÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑŸÖÿ®ÿßÿ¥ÿ± ŸÖŸÜ ÿßŸÑÿ™ÿ∑ÿ®ŸäŸÇ', 'ÿßÿ≥ÿ™ŸÇÿ®ÿßŸÑ ÿ±ÿ≥ÿßÿ¶ŸÑ ÿßŸÑÿ≥ÿ≠ÿ® ŸàÿßŸÑÿ™ÿ≠ŸàŸäŸÑ ÿπŸÑŸâ ÿ™ŸÑÿ¨ÿ±ÿßŸÖ'],
      bg: 'from-orange-50 to-amber-50',
    },
  ];

  // ŸÖÿ±ÿßŸÇÿ®ÿ© ÿ≠ÿßŸÑÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸàÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ ŸÅŸä ÿßŸÑŸàŸÇÿ™ ÿßŸÑŸÅÿπŸÑŸä
  useEffect(() => {
    if (!user?.uid) return;

    console.log('üîç PendingApprovalPage: Setting up real-time subscription monitoring for user:', user.uid);

    const userDocRef = doc(db, 'users', user.uid);
    
    const unsubscribe = onSnapshot(userDocRef, async (doc) => {
      if (doc.exists()) {
        const userData = doc.data();
        
        console.log('üìä PendingApprovalPage: User data updated:', {
          userId: user.uid,
          isApproved: userData.isApproved,
          approved: userData.approved,
          freeTrialActive: userData.freeTrialActive,
          subscription: userData.subscription,
          subscriptionActive: userData.subscription?.isActive
        });
        
        // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ≠ÿßŸÑÿ© ÿßŸÑÿ≠ÿ≥ÿßÿ® ÿßŸÑÿπÿßŸÖÿ© ÿ£ŸàŸÑÿßŸã
        // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ÿßŸÑÿ≠ÿ≥ÿßÿ® ŸÖÿπÿ∑ŸÑÿßŸã (isActive=false)ÿå ŸÜŸÖŸÜÿπ ÿ£Ÿä ÿ™Ÿàÿ¨ŸäŸá ŸÑŸÑŸàÿ≠ÿ© ÿßŸÑÿ™ÿ≠ŸÉŸÖ
        if (userData.isActive === false) {
          console.log('‚õî PendingApprovalPage: User account is deactivated (isActive=false), staying on page.');
          // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ÿßŸÑÿ≠ÿ≥ÿßÿ® ŸÖÿπÿ∑ŸÑÿßŸã ŸÅÿπŸÑÿßŸãÿå ŸÜŸÖÿ≥ÿ≠ ÿπŸÑÿßŸÖÿ© ÿßŸÑÿ∑ÿ±ÿØ ŸÑÿ£ŸÜ ÿßŸÑŸàÿ∂ÿπ ÿ∑ÿ®ŸäÿπŸä
          sessionStorage.removeItem('dashboard_kick_signature');
          return;
        }

        // ÿØÿßŸÑÿ© ŸÖÿ≥ÿßÿπÿØÿ© ŸÑÿ™ŸàŸÑŸäÿØ ÿ™ŸàŸÇŸäÿπ ÿßŸÑÿ≠ÿßŸÑÿ© (ŸÖÿ∑ÿßÿ®ŸÇÿ© ŸÑŸÖÿß ŸÅŸä ProtectedUserDashboard)
        const generateStateSignature = (u: any) => {
          if (!u) return 'null';
          const sub = u.subscription;
          const activatedAt = sub?.activatedAt ? (sub.activatedAt.toDate ? sub.activatedAt.toDate().toISOString() : new Date(sub.activatedAt).toISOString()) : 'null';
          const plan = sub?.planType || 'null';
          const isActive = u.isActive !== false;
          const subActive = sub?.isActive === true;
          const branchActive = u.branchPackage?.status === 'active';
          const branchActivatedAt = u.branchPackage?.activatedAt ? (u.branchPackage.activatedAt.toDate ? u.branchPackage.activatedAt.toDate().toISOString() : new Date(u.branchPackage.activatedAt).toISOString()) : 'null';
          return `${isActive}|${subActive}|${plan}|${activatedAt}|${branchActive}|${branchActivatedAt}`;
        };

        const currentSignature = generateStateSignature(userData);
        const kickedSignature = sessionStorage.getItem('dashboard_kick_signature');

        const isNewState = () => {
             if (!kickedSignature) return true; // Not kicked
             return currentSignature !== kickedSignature;
        };

        // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ≠ÿßŸÑÿ© ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿßŸÑŸÖÿ¨ÿßŸÜŸäÿ© ÿ£Ÿà ÿ®ÿßŸÇÿ© ÿßŸÑŸÅÿ±Ÿàÿπ ÿßŸÑŸÜÿ¥ÿ∑ÿ©
        // ÿ™ŸÖ ÿ•ÿ≤ÿßŸÑÿ© (userData as any)?.freeTrialActive === true ŸÑÿ™ÿ¨ŸÜÿ® ÿßŸÑÿ™Ÿàÿ¨ŸäŸá ÿßŸÑÿÆÿßÿ∑ÿ¶ ÿ®ŸÜÿßÿ°Ÿã ÿπŸÑŸâ ÿ≠ŸÇŸàŸÑ ŸÇÿØŸäŸÖÿ©
        const trialActive = (userData as any)?.isOnFreeTrial === true;
        if (trialActive) {
          if (isNewState()) {
             console.log('‚úÖ PendingApprovalPage: Free trial active, redirecting...');
             sessionStorage.removeItem('dashboard_kick_signature');
             setTimeout(() => { navigate('/user/dashboard', { replace: true }); }, 800);
             return;
          } else {
             console.log('‚õî PendingApprovalPage: Free trial active but state signature matches kicked signature. Loop prevented.');
          }
        }

        const branchActive = (userData as any)?.branchPackage?.status === 'active';
        if (branchActive) {
          if (isNewState()) {
            console.log('‚úÖ PendingApprovalPage: Branch package active, redirecting...');
            sessionStorage.removeItem('dashboard_kick_signature');
            try { await checkSubscriptionStatus(); } catch (e) { console.error(e); }
            setTimeout(() => { navigate('/user/dashboard', { replace: true }); }, 800);
            return;
          } else {
             console.log('‚õî PendingApprovalPage: Branch package active but state signature matches kicked signature. Loop prevented.');
          }
        }

        // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ≠ÿßŸÑÿ© ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ ŸÅŸÇÿ∑ (ÿØŸàŸÜ ÿßŸÑÿßÿπÿ™ŸÖÿßÿØ ÿπŸÑŸâ approved)
        const sub = userData.subscription;
        if (sub && sub.isActive === true) {
          const isValidSubscription = isSubscriptionValid(sub);
          console.log('üîç PendingApprovalPage: Subscription validation result:', {
            hasSubscription: !!sub,
            isActive: sub.isActive,
            isValid: isValidSubscription,
            planType: sub.planType
          });
          if (isValidSubscription) {
            if (isNewState()) {
              console.log('‚úÖ PendingApprovalPage: Valid subscription found, redirecting to dashboard...');
              sessionStorage.removeItem('dashboard_kick_signature');
              
              // Force refresh global context to ensure dashboard sees fresh data
               try {
                 await checkSubscriptionStatus();
               } catch (e) {
                 console.error('Error refreshing subscription status before redirect:', e);
               }
               
               setTimeout(() => { navigate('/user/dashboard', { replace: true }); }, 800);
              return;
            } else {
               console.log('‚õî PendingApprovalPage: Subscription valid but state signature matches kicked signature. Loop prevented.');
            }
          }
        }
      }
    }, (error) => {
      console.error('‚ùå PendingApprovalPage: Error in real-time listener:', error);
    });

    return () => {
      console.log('üîå PendingApprovalPage: Cleaning up real-time listener');
      unsubscribe();
    };
  }, [user?.uid, navigate]);

  // ŸÖÿ≥ÿ™ŸÖÿπ ŸÑÿ•ÿ¥ÿπÿßÿ± ÿ™ŸÅÿπŸäŸÑ ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ ŸÖŸÜ ŸÑŸàÿ≠ÿ© ÿßŸÑÿ•ÿØÿßÿ±ÿ©
  useEffect(() => {
    const handleSubscriptionActivated = (event: CustomEvent) => {
      const { userId, planType } = event.detail;
      console.log('üéâ PendingApprovalPage: Subscription activation event received:', { userId, planType, currentUserId: user?.uid });
      
      if (userId === user?.uid) {
        console.log('‚úÖ PendingApprovalPage: Subscription activated for current user');
        toast.success('ÿ™ŸÖ ÿ™ŸÅÿπŸäŸÑ ÿßÿ¥ÿ™ÿ±ÿßŸÉŸÉ! ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™Ÿàÿ¨ŸäŸá ÿ•ŸÑŸâ ŸÑŸàÿ≠ÿ© ÿßŸÑÿ™ÿ≠ŸÉŸÖ...');
        
        // ŸÖÿ≥ÿ≠ ÿπŸÑÿßŸÖÿ© ÿßŸÑÿ∑ÿ±ÿØ ÿßŸÑÿ≥ÿßÿ®ŸÇ ŸÑŸÑÿ≥ŸÖÿßÿ≠ ÿ®ÿßŸÑÿØÿÆŸàŸÑ
        sessionStorage.removeItem('dashboard_kick_signature');

        // ÿ•ÿπÿßÿØÿ© ŸÅÿ≠ÿµ ÿ≠ÿßŸÑÿ© ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ
        setTimeout(async () => {
          await checkSubscriptionStatus();
          navigate('/user/dashboard', { replace: true });
        }, 1500);
      }
    };

    console.log('üîî PendingApprovalPage: Setting up subscription activation listener');
    window.addEventListener('subscriptionActivated', handleSubscriptionActivated as EventListener);
    
    return () => {
      console.log('üîå PendingApprovalPage: Cleaning up subscription activation listener');
      window.removeEventListener('subscriptionActivated', handleSubscriptionActivated as EventListener);
    };
  }, [user?.uid, navigate, checkSubscriptionStatus]);

  // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ≠ÿßŸÑÿ© ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ ÿπŸÜÿØ ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿµŸÅÿ≠ÿ©
  // ÿ™ŸÖ ÿ•ŸäŸÇÿßŸÅ Ÿáÿ∞ÿß ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÑÿ™ÿ¨ŸÜÿ® ÿßŸÑÿ™Ÿàÿ¨ŸäŸá ÿßŸÑÿÆÿßÿ∑ÿ¶ ŸÑŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ ÿßŸÑŸÖÿπÿ∑ŸÑŸäŸÜ
  // ÿßŸÑÿßÿπÿ™ŸÖÿßÿØ ÿßŸÑŸÉÿßŸÖŸÑ ÿßŸÑÿ¢ŸÜ ÿπŸÑŸâ ÿßŸÑŸÖÿ≥ÿ™ŸÖÿπ ÿßŸÑŸÑÿ≠ÿ∏Ÿä (onSnapshot) ŸÅŸä ÿßŸÑÿ£ÿπŸÑŸâ
  /*
  useEffect(() => {
    console.log('üîç PendingApprovalPage: Checking subscription status on load:', {
      isSubscriptionActive,
      hasUserSubscription: !!userSubscription
    });

    if (isSubscriptionActive) {
      console.log('‚úÖ PendingApprovalPage: Active subscription detected, redirecting...');
      navigate('/user/dashboard', { replace: true });
    }
  }, [isSubscriptionActive, navigate]);
  */

  // ÿ•ÿπÿßÿØÿ© ŸÅÿ≠ÿµ ÿ≠ÿßŸÑÿ© ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ ÿØŸàÿ±ŸäÿßŸã (ÿ™ŸÖ ÿ™ŸÇŸÑŸäŸÑ ÿßŸÑÿ™ŸÉÿ±ÿßÿ±)
  useEffect(() => {
    if (!user?.uid) return;

    const intervalId = setInterval(async () => {
      console.log('üîÑ PendingApprovalPage: Periodic subscription status check');
      await checkSubscriptionStatus();
    }, 30000); // ŸÉŸÑ 30 ÿ´ÿßŸÜŸäÿ© ÿ®ÿØŸÑÿßŸã ŸÖŸÜ 10 ÿ´ŸàÿßŸÜŸê

    return () => {
      clearInterval(intervalId);
    };
  }, [user?.uid, checkSubscriptionStatus]);

  // ÿ™ÿ≠ŸÖŸäŸÑ ÿ≠ÿßŸÑÿ© ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿßŸÑŸÖÿ¨ÿßŸÜŸäÿ© ŸÑŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸÑÿ•ÿÆŸÅÿßÿ° ÿÆŸäÿßÿ± ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿ®ÿπÿØ ÿßÿ≥ÿ™ÿÆÿØÿßŸÖŸáÿß
  useEffect(() => {
    const loadTrialState = async () => {
      if (!user?.uid) return;
      try {
        const data = await FreeTrialService.getTrialData(user.uid);
        setHasUsedTrial(data.hasUsedFreeTrial);
        setIsOnFreeTrial(data.isOnFreeTrial);
      } catch (e) {
        console.error('ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ÿ≠ÿßŸÑÿ© ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿßŸÑŸÖÿ¨ÿßŸÜŸäÿ©:', e);
      }
    };
    loadTrialState();
  }, [user?.uid]);

  useEffect(() => {
    (async () => {
      try {
        const hours = await FreeTrialService.getTrialDurationHours();
        setTrialDurationHours(hours);
      } catch (e) {
        console.error('ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ŸÖÿØÿ© ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿßŸÑŸÖÿ¨ÿßŸÜŸäÿ©:', e);
      }
    })();
  }, []);

  const handleSignOut = async () => {
    await signOut();
    navigate('/auth');
  };

  const handleActivateZeroPlan = async () => {
    if (!user?.uid) return;

    setIsActivatingTrial(true);
    try {
      await activateUserSubscription(user.uid, SubscriptionPlan.ZERO, user.uid);
      toast.success('ÿ™ŸÖ ÿ™ŸÅÿπŸäŸÑ ÿ®ÿßŸÇÿ© ÿßŸÑÿ≤Ÿäÿ±Ÿà! ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™Ÿàÿ¨ŸäŸá...');
      setTimeout(() => {
        try { navigate('/user/dashboard', { replace: true }); } catch {}
      }, 1200);
    } catch (error) {
      console.error('Error activating zero plan:', error);
      toast.error('ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ŸÅÿπŸäŸÑ ÿ®ÿßŸÇÿ© ÿßŸÑÿ≤Ÿäÿ±Ÿà');
    } finally {
      setIsActivatingTrial(false);
    }
  };

  const handleActivateTrial = async () => {
    if (!user?.uid) return;
    
    setIsActivatingTrial(true);
    try {
      const result = await FreeTrialService.startFreeTrial(user.uid);
      if (result.success) {
        toast.success(result.message || `ÿ™ŸÖ ÿ™ŸÅÿπŸäŸÑ ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿßŸÑŸÖÿ¨ÿßŸÜŸäÿ© ŸÑŸÖÿØÿ© ${trialDurationHours} ${trialDurationHours >= 3 && trialDurationHours <= 10 ? 'ÿ≥ÿßÿπÿßÿ™' : trialDurationHours === 2 ? 'ÿ≥ÿßÿπÿ™ŸäŸÜ' : 'ÿ≥ÿßÿπÿ©'}! ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™Ÿàÿ¨ŸäŸá...`);
        setTimeout(() => {
          try { navigate('/user/dashboard', { replace: true }); } catch {}
        }, 1200);
      } else {
        toast.error(result.message);
      }
    } catch (error) {
      console.error('Error activating trial:', error);
      toast.error('ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ŸÅÿπŸäŸÑ ÿßŸÑÿ™ÿ¨ÿ±ÿ®ÿ© ÿßŸÑŸÖÿ¨ÿßŸÜŸäÿ©');
    } finally {
      setIsActivatingTrial(false);
    }
  };

  const handleRefresh = async () => {
    setIsRefreshing(true);
    console.log('üîÑ PendingApprovalPage: Manual refresh triggered');
    
    try {
      // ŸÖÿ≥ÿ≠ ÿπŸÑÿßŸÖÿ© ÿßŸÑÿ∑ÿ±ÿØ ŸÑÿ•ÿ¨ÿ®ÿßÿ± ÿ•ÿπÿßÿØÿ© ÿßŸÑÿ™ÿ≠ŸÇŸÇ
      sessionStorage.removeItem('dashboard_kick_signature');
      
      // ÿ•ÿπÿßÿØÿ© ŸÅÿ≠ÿµ ÿ≠ÿßŸÑÿ© ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ
      await checkSubscriptionStatus();
      
      // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÖÿß ÿ•ÿ∞ÿß ŸÉÿßŸÜ Ÿäÿ¨ÿ® ÿßŸÑÿ™Ÿàÿ¨ŸäŸá ÿßŸÑÿ¢ŸÜ
      if (user?.uid) {
        // ŸÜÿ≥ÿ™ÿÆÿØŸÖ import ÿØŸäŸÜÿßŸÖŸäŸÉŸä ŸÑÿ™ÿ¨ŸÜÿ® ŸÖÿ¥ÿßŸÉŸÑ ÿßŸÑÿ™ÿ®ÿπŸäÿßÿ™ ÿßŸÑÿØÿßÿ¶ÿ±Ÿäÿ© ÿ•ŸÜ Ÿàÿ¨ÿØÿ™ÿå ŸàŸÑÿ∂ŸÖÿßŸÜ ŸÜÿ≥ÿÆÿ© ÿ¨ÿØŸäÿØÿ©
        const { getDoc, doc } = await import('firebase/firestore');
        const userDoc = await getDoc(doc(db, 'users', user.uid));
        
        if (userDoc.exists()) {
          const userData = userDoc.data();
          const accountActive = userData.isActive !== false;
          const subActive = userData.subscription?.isActive === true;
          const branchActive = userData.branchPackage?.status === 'active';
          
          if (accountActive && (subActive || branchActive)) {
             toast.success('ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿ®ŸäÿßŸÜÿßÿ™ÿå ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™Ÿàÿ¨ŸäŸá...');
             setTimeout(() => {
                navigate('/user/dashboard', { replace: true });
             }, 500);
             return;
          }
        }
      }
      
      toast.success('ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿ®ŸäÿßŸÜÿßÿ™');
    } catch (error) {
      console.error('‚ùå Error during manual refresh:', error);
      toast.error('ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿßŸÑÿ™ÿ≠ÿØŸäÿ´');
    } finally {
      setIsRefreshing(false);
    }
  };

  const openWhatsApp = (plan: string, price: number, features: string[]) => {
    try {
      const text = encodeURIComponent(
        `ÿ£ÿ±ÿ∫ÿ® ŸÅŸä ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ ŸÅŸä ${plan} ÿ®ÿ≥ÿπÿ± ${price} ÿ¨\nÿßŸÑŸÖŸÖŸäÿ≤ÿßÿ™:\n- ${features.join('\n- ')}`
      );
      window.open(`https://wa.me/201000392539?text=${text}`, '_blank');
    } catch {}
  };

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <div className="w-full max-w-5xl bg-white rounded-2xl shadow-lg border border-gray-200 dark:border-gray-800 p-4 sm:p-6">
        <div className="text-center">
          <div className="flex justify-center mb-2">
            <div className="relative">
              <div 
                className="absolute inset-0 rounded-full"
                style={{ background: 'linear-gradient(135deg, #f59e0b, #d97706)', filter: 'blur(8px)', opacity: 0.3 }}
              />
              <div className="relative bg-gradient-to-br from-orange-400 to-orange-600 p-2 rounded-full">
                <Shield className="h-10 w-10 text-white" />
              </div>
            </div>
          </div>
          <h2 className="text-xl font-bold text-gray-800">ŸÖÿ∑ŸÑŸàÿ® ÿ™ŸÅÿπŸäŸÑ ÿßŸÑÿ≠ÿ≥ÿßÿ®</h2>
          <p className="text-gray-700 text-sm mt-1">ÿßÿÆÿ™ÿ± ÿßŸÑÿ®ÿßŸÇÿ© ÿßŸÑŸÖŸÜÿßÿ≥ÿ®ÿ© Ÿàÿßÿ∂ÿ∫ÿ∑ ÿ≤ÿ± Ÿàÿßÿ™ÿ≥ÿßÿ® ŸÑŸÑÿ™ŸàÿßÿµŸÑ ÿßŸÑŸÅŸàÿ±Ÿä.</p>
          
          <div className="mt-4 flex justify-center">
             <Button 
               variant="outline" 
               size="sm" 
               onClick={handleRefresh} 
               disabled={isRefreshing}
               className="flex items-center gap-2 text-blue-600 border-blue-200 hover:bg-blue-50"
             >
               <RefreshCw className={`w-4 h-4 ${isRefreshing ? 'animate-spin' : ''}`} />
               {isRefreshing ? 'ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™ÿ≠ÿØŸäÿ´...' : 'ÿ™ÿ≠ÿØŸäÿ´ ÿ≠ÿßŸÑÿ© ÿßŸÑÿßÿ¥ÿ™ÿ±ÿßŸÉ'}
             </Button>
          </div>
        </div>

        <div className="pt-4">
          <div className="grid grid-cols-2 sm:grid-cols-2 lg:grid-cols-4 gap-2 items-stretch">
            {PLANS.map((p) => (
              <div key={p.title} className={`h-full rounded-lg p-2 border shadow-sm bg-gradient-to-br ${p.bg} dark:from-gray-800 dark:to-gray-700 dark:border-gray-700`}>
                <div className="font-bold text-sm text-gray-800 dark:text-gray-100 mb-1">{p.title}</div>
                <div className="text-lg font-extrabold text-gray-900 dark:text-gray-100 mb-2">{p.price} ÿ¨</div>
                <ul className="text-[11px] text-gray-700 dark:text-gray-200 text-right space-y-1 mb-2">
                  {p.features.map((f) => (
                    <li key={f}>‚Ä¢ {f}</li>
                  ))}
                </ul>
                <Button size="sm" className="w-full bg-green-600 hover:bg-green-700 text-xs py-1 h-8" onClick={() => openWhatsApp(p.title, p.price, p.features)}>
                  ÿ™ŸàÿßÿµŸÑ ÿπÿ®ÿ± Ÿàÿßÿ™ÿ≥ÿßÿ®
                </Button>
              </div>
            ))}
          </div>
        </div>

        <div className="pt-3">
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-2 mb-2">
            <Button size="sm" className="w-full bg-emerald-600 hover:bg-emerald-700" onClick={handleActivateZeroPlan} disabled={isActivatingTrial}>
              ÿ¨ÿ±ÿ® ÿ®ÿßŸÇÿ© ÿßŸÑÿ≤Ÿäÿ±Ÿà
            </Button>
            {!hasUsedTrial && !isOnFreeTrial && (
              <Button size="sm" className="w-full bg-blue-600 hover:bg-blue-700" onClick={handleActivateTrial} disabled={isActivatingTrial}>
                {`ÿ¨ÿ±ÿ® ÿßŸÑÿ®ÿ±ŸÜÿßŸÖÿ¨ ${trialDurationHours} ${trialDurationHours >= 3 && trialDurationHours <= 10 ? 'ÿ≥ÿßÿπÿßÿ™' : trialDurationHours === 2 ? 'ÿ≥ÿßÿπÿ™ŸäŸÜ' : 'ÿ≥ÿßÿπÿ©'}`}
              </Button>
            )}
          </div>
          <Button variant="outline" className="border-2 border-gray-300 hover:border-gray-400 text-gray-700 hover:text-gray-800 px-5 py-2 rounded-lg font-semibold text-xs transition-all duration-300" onClick={handleSignOut}>
            ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨
          </Button>
        </div>
      </div>
    </div>
  );
}
